html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    
  }
  
  /* Contenedor raíz */
  #root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  /* Sección Hero */
  .hero {
    color: white;
    text-align: center;
    padding: 30px;
    
  }
  
  .hero h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .hero p {
    font-size: 1.25rem;
    margin-bottom: 20px;
  }
  
  .hero .btn {
    background-color: #0f0636; 
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .hero .btn:hover {
    background-color: #1a1a4b;
  }
  
  /* Contenedor para el mapa y calendario */
  .heroEncuentro {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px; /* Añadir espacio entre las secciones */
    margin-top: 30px;
    padding: 20px;

    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  }
  
  .heroEncuentro h2 {
    font-size: 2rem;
    color: #003366; /* Color de texto azul oscuro */
    margin-bottom: 20px;
  }

.productos-destacado {
    height: fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    
    
}

.productos-destacado a{
    text-decoration: none;
}

.productos-destacado h2 {
    font-size: 2rem;
    margin-bottom: 15px;
}

.cartaPresentacion{
    width: fit-content;
    height: fit-content;
    margin-left: auto;
    margin-right: auto;
    background-color: aliceblue;
    padding:2vh 1vw;
    margin-bottom: 15vh;
}


.productos-destacado:hover{
    transform: scale(1.2);
 }
.imagenMuestra{
    width: auto;
    height: 45vh;
}

.titulo{
  font-size: clamp(1.5rem, 2vh, 2rem);
  line-height: 1.4;
  color: black;
  
}


.pedidosPersonalizados {
    padding: 40px 20px;
    text-align: center;
    background-color: #ececec;
}

.pedidosPersonalizados h2 {
    font-size: 2rem;
    margin-bottom: 15px;
}

footer {
    padding: 20px;
    background-color: #0f0636;
    color: white;
    text-align: center;
    margin-top: auto; 
}
footer p {
    margin: 5px 0;
}

footer a {
    color: #fff;
    text-decoration: underline;
    transition: color 0.3s ease;
}

footer a:hover {
    color: #d3d3d3;
}


@media (max-width: 768px) {
  .heroEncuentro {
    flex-direction: column;
  }

  .map-container iframe {
    height: 300px;
  }
}