/* Contenedor para el mapa */
.map-container {
  background-color: #f0f8ff; /* Fondo más suave y neutro */
  border: 1px solid #007bb5; /* Borde sutil de color azul claro */
  border-radius: 12px; /* Bordes redondeados para un estilo más moderno */
  padding: 20px;
  text-align: center;
  box-shadow: 0px 4px 15px rgba(0, 123, 181, 0.1); /* Sombra más suave */
  width: 100%;
  height: fit-content;
  transition: box-shadow 0.3s ease, border-color 0.3s ease; /* Añadir transiciones suaves */
}

.map-container:hover {
  box-shadow: 0px 6px 20px rgba(0, 123, 181, 0.2); /* Efecto de sombra al pasar el mouse */
  border-color: #005f85; /* Cambio de color en el borde al interactuar */
}

.map-container h2 {
  color: #007bb5;
  font-size: 1.5rem;
  margin-bottom: 15px;
  font-weight: bold;
}

.map-container iframe {
  border-radius: 12px; /* Bordes más redondeados en el iframe */
  width: 100%;
  height: 400px;
  border: none; /* Eliminar borde del iframe */
  transition: transform 0.3s ease; /* Efecto de zoom suave */
}

/* Efecto al pasar el mouse sobre el iframe */
.map-container iframe:hover {
  transform: scale(1.05); /* Le da un ligero zoom al pasar el mouse */
}

/* Responsividad */
@media (max-width: 768px) {
  .map-container iframe {
    height: 300px; /* Reducir altura en pantallas más pequeñas */
  }
}

/* Calendario */
.calendar-container {
  background-color: #e0f7fa; /* Fondo azul claro */
  border: 2px solid #007bb5;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  box-shadow: 0px 4px 12px rgba(0, 123, 181, 0.2);
  width: fit-content;
  height: fit-content;
}

.calendarioDiv {
  display: flex;
  justify-content: center;
  width: 100%;
}

.react-calendar {
  background: rgb(255, 255, 255);
  border: 1px solid #070707;
  color: black;
  font-weight: bold;
}

.react-calendar__month-view__weekdays__weekday abbr {
  text-decoration: none;
  color: rgb(0, 4, 211);
}

.react-calendar__tile {
  border-radius: 50%;
}

/* Estilos para días resaltados */
.react-calendar__tile.highlight {
  background-color: #340aee;
  color: white;
  font-weight: bold;
}

.react-calendar__tile.highlight:hover {
  transform: scale(1.1);
}
