.map-container {
    background-color: #e0f7fa; /* Fondo azul claro */
    border: 2px solid #007bb5;
    border-radius: 10px;
    padding: 20px;
    text-align: center;
    box-shadow: 0px 4px 12px rgba(0, 123, 181, 0.2);
    width: 100%;
    height: fit-content;
  }
  
  .map-container h2 {
    color: #1173a0;
    font-size: 1.5rem;
    margin-bottom: 15px;
    font-weight: bold;
  }
  
  .map-container iframe {
    border-radius: 8px;
    width: 100%;
    height: 60vh;
  }